/*
==========================
STYLING
==========================
*/
/*
==========================
GRID
==========================
*/
.individual-project-container {
  margin: 0 auto;
  color: #757575; }

/*
==========================
IMAGE
==========================
*/
.project-image {
  width: 900px;
  height: auto;
  -o-object-fit: cover;
     object-fit: cover;
  margin: 0 auto;
  margin-top: 40px;
  -webkit-box-shadow: 0px 15px 15px #d1d1d1;
          box-shadow: 0px 15px 15px #d1d1d1;
  display: block; }

.project-image-padding {
  margin-top: 80px; }

/*
==========================
MAIN INFO
==========================
*/
.project-page-title {
  font-size: 40px;
  text-align: center;
  padding-top: 40px;
  padding-bottom: 10px; }

.project-page-description {
  width: 560px;
  text-align: center;
  font-size: 16px;
  margin: 0 auto; }

.project-page-tech {
  width: 500px;
  margin: 0 auto;
  display: block;
  text-align: center;
  margin-top: 20px; }

/*
==========================
FEATURE
==========================
*/
.hline {
  width: 1080px;
  border-top: 1px solid #d1d1d1;
  margin-top: -2px; }

.project-page-feature {
  background: -webkit-gradient(linear, left top, left bottom, from(white), to(#f7f7f7));
  background: -webkit-linear-gradient(white, #f7f7f7);
  background: -o-linear-gradient(white, #f7f7f7);
  background: linear-gradient(white, #f7f7f7);
  padding: 50px; }

.project-page-feature-title {
  font-size: 20px;
  font-weight: 600;
  text-align: center; }

.project-page-feature-description {
  width: 550px;
  margin: 0 auto;
  text-align: center;
  font-size: 16px;
  margin-top: 10px; }

/*
==========================
WORK
==========================
*/
.project-page-work {
  display: block;
  width: 560px;
  margin: 0 auto;
  margin-top: 20px;
  text-align: left; }

.role-container {
  display: block;
  margin: 10px; }

.role-dot {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  width: 10px;
  height: 10px;
  background-color: #8fb6df;
  display: inline-block;
  margin: 0 auto;
  margin-right: 10px; }

.role {
  display: inline-block; }

/*
==========================
BUTTON
==========================
*/
.project-page-btns {
  display: block;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 20px;
  text-align: center; }

.main-navi-btn {
  display: inline-block;
  width: 150px;
  color: white;
  background-color: #3681d1;
  padding: 6px 15px;
  margin: 10px;
  border-radius: 25px;
  text-align: center;
  text-decoration: none;
  -webkit-box-shadow: 5px 5px 10px #d1d1d1;
          box-shadow: 5px 5px 10px #d1d1d1; }

.main-navi-btn:hover {
  background-color: #2673c5; }

/*
==========================
MEDIA QUERY
==========================
*/
@media all and (max-width: 1079px) and (min-width: 0px) {
  .project-image {
    width: 80vw; }
  .project-page-description {
    width: 450px; } }

@media (max-width: 650px) and (min-width: 0px) {
  .project-image {
    width: 90vw; }
  .project-page-title {
    font-size: 35px;
    font-weight: 600; }
  .project-page-description {
    width: 80vw;
    font-size: 14px;
    text-align: left; }
  .project-page-work {
    width: 90vw;
    text-align: left;
    font-size: 14px; }
  .role-container {
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 10px; }
  .project-page-tech {
    width: 90vw; }
  .project-page-feature {
    padding: 10px;
    padding-bottom: 40px;
    padding-top: 30px; }
  .project-page-feature-title {
    margin: 0 auto;
    max-width: 90vw;
    margin-bottom: 10px;
    font-size: 20px; }
  .project-page-feature-description {
    margin: 0 auto;
    width: 90vw;
    font-size: 14px;
    margin-bottom: -20px; } }



