/*
==========================
STYLING
==========================
*/
.about-page-container {
  z-index: 1;
  position: absolute;
  display: block;
  top: 200px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  max-width: 1000px;
  background-color: rgba(255, 255, 255, 0.8);
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-areas: "user bio bio";
  color: #757575;
  padding: 80px 0px;
  -webkit-box-shadow: 0px 3px 6px #d1d1d1;
          box-shadow: 0px 3px 6px #d1d1d1;
  justify-items: center;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 50px;
  border: 2px solid #f8f8f8; }

/*
==========================
USER INFO
==========================
*/
.user-info-section {
  grid-area: user;
  text-align: center; }

.profile-img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  -o-object-fit: cover;
     object-fit: cover;
  -webkit-box-shadow: 0px 3px 6px #d1d1d1;
          box-shadow: 0px 3px 6px #d1d1d1; }

.name {
  font-size: 26px;
  margin-top: 20px; }

.location {
  font-size: 14px; }

/*
==========================
USER LINKS
==========================
*/
.user-links-section {
  margin-top: 10px; }

.user-link-img {
  width: 40px;
  height: 40px;
  -o-object-fit: cover;
     object-fit: cover;
  margin: 8px; }

/*
==========================
DESCRIPTION
==========================
*/
.description {
  grid-area: bio;
  padding: 0px 50px;
  border-left: 1px solid #d1d1d1;
  font-size: 18px;
  line-height: 30px; }

.bio-link {
  color: #8fb6df;
  display: inline-block; }

/*
==========================
MEDIA QUERY
==========================
*/
@media all and (max-width: 1079px) and (min-width: 0px) {
  .about-page-container {
    width: 80vw;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    grid-template-columns: repeat(1, 1fr);
    grid-template-areas: "user" "bio"; }
  .user-info-section {
    padding-bottom: 50px; }
  .description {
    border: none;
    border-top: 1px solid #d1d1d1;
    padding-top: 50px; }
  .bio-link {
    margin-left: 8px; } }

@media all and (max-width: 500px) and (min-width: 0px) {
  .profile-img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    -o-object-fit: cover;
       object-fit: cover;
    -webkit-box-shadow: 0px 3px 6px #d1d1d1;
            box-shadow: 0px 3px 6px #d1d1d1; }
  .description {
    padding: 30px;
    font-size: 16px; }
  .about-page-container {
    padding-bottom: 0px;
    top: 100px; } }



