/*
==========================
GRID & PROJECTS CONTAINER
==========================
*/
.projects-container {
  max-width: 1080px;
  margin: 0 auto; }

.projects-border {
  border-left: 1px solid #d1d1d1; }

.projects {
  margin-left: 180px;
  padding: 30px;
  padding-right: 0px;
  color: #757575; }

.projects-container-title {
  color: #95989a;
  text-align: center;
  margin: 0 auto;
  font-size: 30px;
  margin-top: 100px;
  margin-bottom: 20px;
  font-weight: 600;
  letter-spacing: 3px;
  text-transform: uppercase; }

/*
==========================
PROJECT
==========================
*/
.project {
  max-width: 900px;
  padding-top: 30px;
  padding-bottom: 30px;
  border-top: 1px solid #d1d1d1;
  display: grid;
  grid-template-areas: "d d d i";
  grid-template-columns: repeat(4, 1fr);
  text-align: left;
  grid-gap: 20px; }

/*
==========================
PROJECT INFO
==========================
*/
.project-info {
  grid-area: d; }

.project-title {
  font-size: 40px; }

.project-tech {
  padding-bottom: 15px; }

.project-description {
  font-size: 16px;
  line-height: 28px;
  padding-bottom: 30px; }

.project-tech-skill {
  border: 1px solid #d1d1d1;
  padding: 4px 10px;
  font-size: 12px;
  display: inline-block;
  margin-right: 10px;
  margin-top: 10px; }

/*
==========================
IMAGE
==========================
*/
.project-coverPhoto {
  grid-area: i;
  width: 350px;
  height: 350px;
  -o-object-fit: cover;
     object-fit: cover; }

/*
==========================
BUTTON
==========================
*/
.btn-more {
  color: #8fb6df;
  text-decoration: none;
  border: 1px solid #8fb6df;
  padding: 5px 15px; }

.btn-more:hover {
  background-color: #E4F4F6; }

/*
==========================
TIMELINE
==========================
*/
.project-timeline-dot {
  position: relative;
  margin-left: -45px;
  margin-bottom: -20px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: white;
  border: 8px solid #d1d1d1;
  display: inline-block; }

.project-timeline-dot:hover {
  border: 8px solid #8fb6df; }

.project-timeline-text {
  position: relative;
  top: 15px;
  display: inline-block;
  text-align: right;
  width: 120px;
  margin-left: -160px;
  color: #95989a; }

/*
==========================
MEDIA QUERY
==========================
*/
@media all and (max-width: 1079px) {
  .project {
    max-width: 65%;
    grid-template-areas: "i i i i" "d d d d"; }
  .projects {
    margin-left: 20%;
    width: 100%; }
  .project-coverPhoto {
    width: 100%;
    height: auto; } }

@media all and (max-width: 600px) {
  .project {
    max-width: 90%;
    grid-template-areas: "i i i i" "d d d d"; }
  .projects {
    margin-left: 0%; }
  .projects-border {
    border-left: none; }
  .project-timeline-dot {
    display: none; }
  .project-timeline-text {
    display: none; }
  .project-coverPhoto {
    width: 100%;
    height: auto; }
  .project-title {
    font-size: 30px; }
  .project-description {
    font-size: 14px; } }



