/*
==========================
STYLING
==========================
*/
.landing-container {
  height: 100vh;
  overflow: hidden;
  overflow-y: hidden;
  overflow-x: hidden; }

.landing-shapes {
  height: 100vh; }

/*
==========================
TEXT
==========================
*/
.landing-text,
.landing-description {
  left: 0px;
  right: 0px;
  margin: 0 auto;
  padding: 0 auto; }

.landing-text {
  position: absolute;
  top: 55vh;
  text-align: center;
  color: #757575; }

.landing-name {
  font-size: 40px;
  padding-bottom: 20px; }

.landing-description {
  font-size: 18px;
  width: 550px; }

/*
==========================
BUTTON
==========================
*/
.btn-navi {
  text-align: center;
  color: white;
  background-color: #3681d1;
  top: 30px;
  padding: 10px 50px;
  border-radius: 20px;
  position: relative;
  text-decoration: none;
  -webkit-box-shadow: 5px 5px 10px #d1d1d1;
          box-shadow: 5px 5px 10px #d1d1d1; }

.btn-navi:hover {
  background-color: #2673c5; }

/*
==========================
COLORS
==========================
*/
.pink {
  background-color: #F6E4E4;
  margin-top: 120px;
  margin-left: -5vw; }

.yellow {
  background-color: #F6F1E4;
  right: 0px;
  top: 600px; }

.blue {
  background-color: #E4F4F6;
  top: 680px;
  left: 25vw; }

.green {
  background-color: #EAF6E4;
  right: 10vw;
  top: 150px; }

.purple {
  right: 30vw;
  top: 400px; }

/*
==========================
SHAPES
==========================
*/
.diamond-large, .diamond, .circle-small, .circle-big, .triangle {
  position: absolute; }

.diamond, .diamond-large {
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-name: rotate;
          animation-name: rotate;
  -webkit-animation-duration: 60s;
          animation-duration: 60s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite; }

.diamond {
  width: 400px;
  height: 400px; }

.diamond-large {
  width: 475px;
  height: 475px;
  -webkit-animation-direction: reverse;
          animation-direction: reverse;
  -webkit-animation-delay: 3s;
          animation-delay: 3s; }

.circle-small, .circle-big {
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-name: pulse;
          animation-name: pulse;
  -webkit-animation-duration: 7s;
          animation-duration: 7s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite; }

.circle-small {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  -webkit-animation-delay: 3s;
          animation-delay: 3s; }

.circle-big {
  width: 275px;
  height: 275px;
  border-radius: 50%; }

.triangle {
  width: 0px;
  height: 0px;
  border-left: 200px solid transparent;
  border-top: 200px solid #E9E4F6; }

/*
==========================
ANIMATION
==========================
*/
@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }
@keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  50% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  50% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

/*
==========================
MEDIA QUERIES
==========================
*/
@media all and (max-width: 1500px) and (min-width: 0px) {
  .diamond {
    width: 20vw;
    height: 20vw; }
  .diamond-large {
    width: 30vw;
    height: 30vw; }
  .blue {
    left: 15vw;
    top: 85vh; }
  .green {
    right: 5vw;
    top: 75px; }
  .purple {
    right: 20vw;
    top: 30vh; }
  .circle-big {
    width: 15vw;
    height: 15vw; }
  .circle-small {
    width: 13vw;
    height: 13vw; }
  .triangle {
    border-left: 15vw solid transparent;
    border-top: 15vw solid #E9E4F6; }
  .yellow {
    top: 65vh; } }

@media all and (max-width: 700px) {
  .landing-text {
    max-width: 90vw;
    left: 0px;
    margin: 0 auto; }
  .landing-description {
    width: 100%; } }



